
import goodsCard from "../../components/goodsCard/goodsCard.vue";
import Search from 'vux/src/components/search/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Loading from 'vux/src/components/loading/index.vue'

export default {
components: {
Search,
goodsCard,
ViewBox,
Flexbox,
FlexboxItem,
Loading
},
methods: {
setFocus() {
this.$refs.search.setFocus();
},
resultClick(item) {
window.alert("you click the result item: " + JSON.stringify(item));
},
getResult(val) {

console.log("输入了", val);
},
onSubmit() {

var _this = this;

var value = this.value;
console.log("最终输入的字是：", value);
this.$post(
"goods/query", {
key: value,
},
res => {
console.log(res);
if(res.res >= 1) {
_this.show = false;
}
if(res < 0) {

}

}
);
},
onFocus() {
console.log("on focus");
},
onCancel() {
console.log("点击了取消");

this.$router.go(-1);
}
},
data() {
return {
show: false,
results: [],
value: "限量版来袭",
goodsList: []
};
},
mounted() {
this.$get(
this.$serverHome + "goods/getList", {
page: this.currentPage,
limit: this.pageSize
},
res => {
console.log(res);
this.goodsList = res.msg;
}
);
}
};



